code[class*="language-"], pre[class*="language-"] {
  color: #fff;
  text-align: left;
  text-shadow: 0 -.1em .2em #000;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: none;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  font-size: 1em;
  line-height: 1.5;
}

pre[class*="language-"], :not(pre) > code[class*="language-"] {
  background: #141414;
}

pre[class*="language-"] {
  border: .3em solid #545454;
  border-radius: .5em;
  margin: .5em 0;
  padding: 1em;
  overflow: auto;
  box-shadow: inset 1px 1px .5em #000;
}

pre[class*="language-"]::selection {
  background: #27292a;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection, code[class*="language-"]::selection, code[class*="language-"] ::selection {
  text-shadow: none;
  background: rgba(237, 237, 237, .15);
}

:not(pre) > code[class*="language-"] {
  white-space: normal;
  border: .13em solid #545454;
  border-radius: .3em;
  padding: .15em .2em .05em;
  box-shadow: inset 1px 1px .3em -.1em #000;
}

.token.comment, .token.prolog, .token.doctype, .token.cdata {
  color: #787878;
}

.token.punctuation, .token.namespace {
  opacity: .7;
}

.token.tag, .token.boolean, .token.number, .token.deleted {
  color: #cf694a;
}

.token.keyword, .token.property, .token.selector, .token.constant, .token.symbol, .token.builtin {
  color: #f9ee9a;
}

.token.attr-name, .token.attr-value, .token.string, .token.char, .token.operator, .token.entity, .token.url, .language-css .token.string, .style .token.string, .token.variable, .token.inserted {
  color: #919e6b;
}

.token.atrule {
  color: #7386a5;
}

.token.regex, .token.important {
  color: #e9c163;
}

.token.important, .token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.language-markup .token.tag, .language-markup .token.attr-name, .language-markup .token.punctuation {
  color: #ad895c;
}

.token {
  z-index: 1;
  position: relative;
}

.line-highlight.line-highlight {
  z-index: 0;
  background: linear-gradient(to right, rgba(84, 84, 84, .1) 70%, rgba(84, 84, 84, 0));
  border-top: 1px dashed #545454;
  border-bottom: 1px dashed #545454;
  margin-top: .75em;
}

.line-highlight.line-highlight:before, .line-highlight.line-highlight[data-end]:after {
  color: #f5f2f0;
  background-color: #8794a6;
}

